
import { NCanvas } from '..';

import { AppCoreWrapper } from './AppCoreWrapper';
import { ControllerCoreWrapper } from './ControllerCoreWrapper';
import { GraphicsCoreWrapper } from './GraphicsCoreWrapper';

import { ModelViewCoreWrapper } from './ModelViewCoreWrapper';

export class Model {
    app: NCanvas;
    appCoreWrapper: AppCoreWrapper;
    controllerCoreWrapper: ControllerCoreWrapper;
    graphicsCoreWrapper: GraphicsCoreWrapper;

    modelViewCoreWrapper: ModelViewCoreWrapper;
    constructor(app: NCanvas, id: string) {
        this.app = app;

        this.appCoreWrapper = new AppCoreWrapper(this, id);
        this.modelViewCoreWrapper = new ModelViewCoreWrapper(this, id);

        this.controllerCoreWrapper = new ControllerCoreWrapper(this);
        this.graphicsCoreWrapper = new GraphicsCoreWrapper(this);
        this.Init();

    }
    private Init() {
        this.appCoreWrapper.ioWrapper.InitEventListener(this.modelViewCoreWrapper.viewWrapper.div as HTMLDivElement);
        // this.controllerCoreWrapper.activeCommandController.InitDynamic();
        // this.controllerCoreWrapper.activeCommandController
    }
    Restart() {
        console.log('re start!!');
    }
    Destroy() {
        this.graphicsCoreWrapper.rendererWrapper.renderer.isActive = false;
    }
    Resize(width: number, height: number) {
        this.graphicsCoreWrapper.canvasWrapper.canvas.ResizeCanvas(width, height);
        this.modelViewCoreWrapper.viewWrapper.div.style.width = width + 'px';
        this.modelViewCoreWrapper.viewWrapper.div.style.height = height + 'px';
    }
}