// import { CLICK_TYPE } from '../../../../lib';
import { MouseEventData } from '../../../../Model/AppCoreWrapper/IOWrapper/EventInteraction/MouseInteraction';
import { ViewIOWrapper } from '..';

export class EventInteraction {
    viewIO: ViewIOWrapper;
    private _propagation: boolean = false;
    constructor(viewIO: ViewIOWrapper) {
        this.viewIO = viewIO;
        const mouseInteraction = this.viewIO.viewAppCoreWrapper.viewController.app.model.appCoreWrapper.ioWrapper.mouseInteraction;
        mouseInteraction.mouseInteractionUI = this;
    }
    MouseInteractionMenu(m: MouseEventData) {
        this.viewIO.viewAppCoreWrapper.viewController.NUIWrapper.NUI.MouseInteractionMenu(m);
    }
    set stopPropagation(v: boolean) {
        this.viewIO.viewAppCoreWrapper.viewController.app.model.appCoreWrapper.ioWrapper.mouseInteraction.stopPropagation = v;
        this._propagation = v;
    }
    get stopPropagation() {
        return this._propagation;
    }
}