import { ViewControllerWrapper } from '..';

export class ViewActiveCommand {
    viewControllerWrapper: ViewControllerWrapper;
    constructor(viewControllerWrapper: ViewControllerWrapper) {
        this.viewControllerWrapper = viewControllerWrapper;
    }
    Resize(width: number, height: number) {
        this.viewControllerWrapper.viewController.NUIWrapper.Resize(width, height);
    }
}