import { NCanvas } from '..';

import { MouseEventData } from '../Model/AppCoreWrapper/IOWrapper/EventInteraction/MouseInteraction';

export class Implementation {
    app: NCanvas;

    // tslint:disable-next-line:no-any
    SceneCreativeCoding: any;
    // tslint:disable-next-line:no-any
    SceneVis: any;
    // tslint:disable-next-line:no-any
    SceneVisMapArchGIS: any;

    constructor(app: NCanvas) {
        this.app = app;
    }
    async InitCreativeCoding() {
        return import('./SceneCreativeCoding').then(({ SceneCreativeCoding }) => {
            this.SceneCreativeCoding = new SceneCreativeCoding(this);
        }).catch(err => {
            console.log('Stop loading NCanvas implementation, SceneCreativeCoding.'); console.log(err);
        });
    }
    async InitSceneVis() {
        return import('./SceneVis').then(({ SceneVis }) => {
            this.SceneVis = new SceneVis(this);
        }).catch(err => {
            console.log('Stop loading NThree implementation, SceneVis.'); console.log(err);
        });
    }
    // .............................................................................................................
    MouseEventController(e: MouseEventData) {
        if (this.SceneCreativeCoding) { this.SceneCreativeCoding.activeCommandController.MouseEventController(e); }
        if (this.SceneVis) { this.SceneVis.activeCommandController.MouseEventController(e); }
    }
    Render(ctx: CanvasRenderingContext2D) {
        if (this.SceneCreativeCoding) { this.SceneCreativeCoding.Render(ctx); }
        if (this.SceneVis) { this.SceneVis.Render(ctx); }
    }
    RemoveAllScene() {
        if (this.SceneCreativeCoding) { this.SceneCreativeCoding.Dispose(); } this.SceneCreativeCoding = undefined;
        if (this.SceneVis) { this.SceneVis.Dispose(); } this.SceneVis = undefined;
    }
    ShiftScene(v: string) {
        this.RemoveAllScene();
        if (v === 'canvasVisualization') {
            this.InitSceneVis();
        } else if (v === 'creativeCoding') {
            this.InitCreativeCoding();
        }
    }
    LoadDefinition(v0: string, v1: string) {

        console.log(v0, v1);
        if (v0 === 'creativeCoding') {
            if (this.SceneCreativeCoding) { this.SceneCreativeCoding.LoadDefinition(v1); }
        } else if (v0 === 'canvasVisualization') {
            if (this.SceneVis) { this.SceneVis.LoadDefinition(v1); }
        } else if (v0 === 'visMap') {
            if (this.SceneVisMapArchGIS) { this.SceneVisMapArchGIS.LoadDefinition(v1); }
        }
    }
}