
import { Model } from '..';

import { LogWrapper } from './LogWrapper';
import { UIWrapper } from './UIWrapper';
import { ViewWrapper } from './ViewWrapper';

export class ModelViewCoreWrapper {
    model: Model;
    logWrapper: LogWrapper;
    uiWrapper: UIWrapper;
    viewWrapper: ViewWrapper;
    constructor(model: Model, id: string, width: number = 100, height: number = 100) {
        this.model = model;
        this.logWrapper = new LogWrapper(this);
        this.uiWrapper = new UIWrapper(this);
        this.viewWrapper = new ViewWrapper(this, id, width, height);
    }
}