import { AppProperties, BrowserUtility, AppAccessibility } from '../../lib';

import { Model } from '..';

import { IOWrapper } from './IOWrapper';

export class AppCoreWrapper {
    model: Model;

    public appProperties: AppProperties;
    public appAccessibility: AppAccessibility;
    public browserUtility: BrowserUtility;

    ioWrapper: IOWrapper;

    constructor(model: Model, id: string, ) {
        this.model = model;

        this.appProperties = new AppProperties();
        this.browserUtility = new BrowserUtility();

        this.ioWrapper = new IOWrapper(this);
    }
}