import { NUI } from '../../lib';

import { ViewController } from '../index';

export class NUIWrapper {
    NUI: NUI;
    viewController: ViewController;
    constructor(viewController: ViewController) {
        this.viewController = viewController;
        this.NUI = new NUI(this.viewController.app.model.modelViewCoreWrapper.viewWrapper.div);
        this.BindingActiveCommandController();
    }
    BindingActiveCommandController() {
        this.NUI.BindingActiveCommandController('NCanvas', this.viewController.app.model.controllerCoreWrapper.activeCommandController);
    }
    Resize(width: number, height: number) {
        this.NUI.Resize(width, height);
    }
    Destroy() {
        this.NUI.Destroy();
    }
}